import React from 'react';

import STYLES from './Footer.module.scss';
import ContactInfo from '../ContactInfo';
import { Profile } from '../../core/Profile';
import { Settings } from '../../config/Settings';

const getClassName = (className: string) => STYLES[className] || 'UNKNOWN';

const Footer = () => {
  const profile: Profile = Settings.getProfileInstance();
  const currentYear: number = new Date().getFullYear();

  return (
    <footer className={getClassName('Footer')}>
      <section className={getClassName('Footer__contacts')}>
        <div className="container">
          <div className="row">
            <div className="col-12 col-sm-4">
              <ContactInfo contact={profile.contact.location} />
            </div>
            <div className="col-12 col-sm-4">
              <ContactInfo contact={profile.contact.phone} />
            </div>
            <div className="col-12 col-sm-4">
              <ContactInfo contact={profile.contact.email} />
            </div>
          </div>
        </div>
      </section>
      <section className={getClassName('Footer__copyright')}>
        <p>
          Copyright &copy;
          {` ${currentYear} All rights reserved`}
        </p>
      </section>
    </footer>
  );
};

export default Footer;
