import settings from '../resources/settings.json';
import { Profile, SocialLink } from '../core/Profile';

class Settings {
  static getProfileInstance = (): Profile => {
    const socialLinks: SocialLink[] = settings.profile.socialLinks.map(
      (link) => {
        return {
          name: link.name,
          icon: link.icon,
          link: link.url
        };
      }
    );

    return {
      name: settings.profile.name,
      photoUrl: settings.profile.image,
      socialLinks: socialLinks,
      contact: {
        location: settings.profile.contact.location,
        phone: settings.profile.contact.phone,
        email: settings.profile.contact.email
      }
    };
  };
}

export { Settings };
