const getClassName = (className: string) => STYLES[className] || 'UNKNOWN';

const App = () => {
  return (
    <main className={getClassName('App')}>
      <section
        className={getClassName('App__profile-info-container')}
        style={{ backgroundImage: `url(${backgroundImage})` }}
      >
        <div className="container mt-5">
          <div className="row justify-content-center">
            <div className="col-11 col-lg-7 align-self-center">
              <Header />
              <div
                className={getClassName('App__profile-info-container__content')}
              >
                <ProfileIntro />
              </div>
            </div>
          </div>
        </div>
      </section>
      <section className={getClassName('App__profile-links-container')}>
        <ProfileLinks />
      </section>

      <Footer />
    </main>
  );
};

export default App;
