import React from 'react';

import STYLES from './ProfileIntro.module.scss';
import { Settings } from '../../config/Settings';
import { Profile } from '../../core/Profile';

const getClassName = (className: string) => STYLES[className] || 'UNKNOWN';

const ProfileIntro = () => {
  const profile: Profile = Settings.getProfileInstance();

  return (
    <div className={getClassName('ProfileIntro')}>
      <div className="row justify-content-center">
        <div className="col-8 col-lg-6 align-self-center">
          <p>
            Hi, I'm <b>{profile.name}.</b>
          </p>
          <p>I'm a Software Engineer.</p>
          <p>I work with Java, Kotlin, and JavaScript.</p>
          <p>
            I'm enthusiastic about AI and Machine Learning, and at the same time
            obsessed with FinTech and Blockchain.
          </p>
        </div>
      </div>
    </div>
  );
};

export default ProfileIntro;
