import React from 'react';

import STYLES from './ProfilePicture.module.scss';
import { Settings } from '../../config/Settings';
import { Profile } from '../../core/Profile';
import profilePicture from '../../resources/imgs/profile.png';

const getClassName = (className: string) => STYLES[className] || 'UNKNOWN';

const ProfilePicture = () => {
  const profile: Profile = Settings.getProfileInstance();

  return (
    <div className={getClassName('ProfilePicture')}>
      <img src={profilePicture} alt={profile.name} loading="lazy" />
    </div>
  );
};

export default ProfilePicture;
