import React from 'react';

import STYLES from './ProfileLinks.module.scss';
import { Settings } from '../../config/Settings';
import { Profile, SocialLink } from '../../core/Profile';
import ProfileLink from '../ProfileLink';

const getClassName = (className: string) => STYLES[className] || 'UNKNOWN';

const ProfileLinks = () => {
  const profile: Profile = Settings.getProfileInstance();
  return (
    <div className={getClassName('ProfileLinks')}>
      <ul>
        {profile.socialLinks.map((socialLink: SocialLink, index: number) => (
          <ProfileLink index={index} link={socialLink} />
        ))}
      </ul>
    </div>
  );
};

export default ProfileLinks;
