import React from 'react';

import STYLES from './ContactInfo.module.scss';

const getClassName = (className: string) => STYLES[className] || 'UNKNOWN';

const ContactInfo = (props: any) => {
  const { contact } = props;
  const { icon, content, other } = contact;

  return (
    <div className={getClassName('ContactInfo')}>
      <i className={icon} />
      <div>
        <h5>{content}</h5>
        <h6>{other}</h6>
      </div>
    </div>
  );
};

export default ContactInfo;
